@import "./var";

.loading__title {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 40px;
  height: 40px;
  border: 10px solid $white;
  border-top: 10px solid $blackShadow;
  border-radius: 50em;
  animation-name: loading;
  animation-duration: 0.8s;
  animation-iteration-count: infinite;
}

.updated {
  font-size: $small;
  margin-left: 1em;
}

@keyframes loading {
  from {
    transform: translate(-50%, -50%) rotate(0);
  }
  to {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

.error__title {
  display: flex;
  width: 100%;
  height: 90vh;
  overflow: hidden;
  justify-content: center;
  align-items: center;
}
